
.w-r {
    margin: 0.45rem;
    border: 0.05rem solid #ccc;
    border-radius: 10px;
    background: #f6f6f6;
    padding: 10px;
}
.btn-pay {
    margin: 20px auto 0;
    width: 90%;
    height: 40px;
    font-size: 16px;
    color: #fff;
    background: #80D983;
    border: 1px solid #47D14C;
    border-radius: 3px;
    line-height: 40px;
    text-align: center;
}
.shuru {
    position: relative;
    margin-bottom: 10px;
}
.shuru span {
    position: absolute;
    top: 5px;
    font-size: 25px;
}
.shuru div {
    border: none;
    width: 100%;
    height: 50px;
    font-size: 25px;
    line-height: 50px;
    border-bottom: 1px solid #CFCFCF;
    text-indent: 30px;
    outline: none;
    white-space: pre;
    overflow-x: scroll;
}
.w-r p {
    font-size: 14px;
    color: #8D8D8F;
}



